@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  font-size: 62.5%;

}

.carusel {
  display: flex;
  height: 20rem;
  width: 100%;
  margin-inline: auto;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.profile{
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 10rem;
  width: 10rem;
  border-radius: 50%;
}
.lenguage-icon{
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  height: 8rem;
}
@media (min-width: 768px) {
  .carusel {
    height: 35rem;
    width: 75%;
    background-size: cover;
  }
  .profile{
    width: 20rem;
    height: 20rem;
  }
}

@media (min-width: 1023px) {
  .carusel {
    height: 45rem;
    width: 75%;
    background-size: cover;
  }

}